.link-decoration(@base: underline);

a {
  .transition(~"all 0.3s linear"); // Double decl for Safari
  .transition(~"color 0.3s linear, text-decoration-color 0.15s linear");
}

a {
  .new-window-link {
    .fa();
    font-size: @font-size-small;
    padding-left: 3px;
    margin-right: 2px;
    color: @gray-light;
    .transition(color 0.3s linear);

    &:before {
      content: @fa-var-external-link;
    }
  }

  &:hover .new-window-link {
    color: @link-color;
  }
}

.lead {
  font-weight: 700;
}

// ID-105 Reduce heading sizes on XS
h1, .h1 { font-size: ceil((@font-size-h1 * @font-scale-xs)); }
h2, .h2 { font-size: ceil((@font-size-h2 * @font-scale-xs)); }
h3, .h3 { font-size: ceil((@font-size-h3 * @font-scale-xs)); }
h4, .h4 { font-size: ceil((@font-size-h4 * @font-scale-xs)); }
h5, .h5 { font-size: ceil((@font-size-h5 * @font-scale-xs)); }
h6, .h6 { font-size: ceil((@font-size-h6 * @font-scale-xs)); }

@media (min-width: @grid-float-breakpoint) {
  h1, .h1 { font-size: @font-size-h1; }
  h2, .h2 { font-size: @font-size-h2; }
  h3, .h3 { font-size: @font-size-h3; }
  h4, .h4 { font-size: @font-size-h4; }
  h5, .h5 { font-size: @font-size-h5; }
  h6, .h6 { font-size: @font-size-h6; }
}

// ID-105 Reduce spacing around major headings on XS
h1, .h1,
h2, .h2,
h3, .h3 {
  margin-top: (@line-height-computed / 2);
}

@media (min-width: @grid-float-breakpoint) {
  h1, .h1,
  h2, .h2,
  h3, .h3 {
    margin-top: @line-height-computed;
  }
}

h1 {
  padding-bottom: (@line-height-computed / 2);
  border-bottom: 1px solid @id7-subtle-line-colour;
}

h6, .h6 {
  font-weight: @id7-h6-font-weight;
}

h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6 {
  &:first-child {
    margin-top: 0;
  }
}

.id7-page-title {
  margin-top: (@grid-gutter-width / 2);

  h1 {
    margin-bottom: 0;
  }
}

.bordered {
  border-bottom: 1px solid @id7-subtle-line-colour;
}

blockquote.quotes {
  border-left: none;

  &::before {
    position: absolute;
    content: '\201C';
    color: @id7-brand-gray;
    margin-left: -24px;
    margin-top: -10px;
    font-size: 4em;
    line-height: 1em;
  }

  &.pull-right, &.blockquote-reverse {
    border-right: none;

    &::before {
      margin-left: 4px;
      content: '\201D';
    }
  }

  @media print {
    border: none;
  }
}

