// ID-113
body {
  text-rendering: optimizeLegibility;

  font-feature-settings: "kern", "liga"; /* standard */
  font-variant-ligatures: common-ligatures;
  font-kerning: normal;
}

// ID-127
.id7-main-content-area {
  ul:not(.nav):not(.dropdown-menu):not(.breadcrumb):not(.pagination):not(.pager):not(.list-group), ol {
    padding-left: 30px;

    li {
      margin-bottom: 6px;
    }

    ul, ol {
      margin-top: 6px;
    }
  }

  ul:not(.nav):not(.dropdown-menu):not(.breadcrumb):not(.pagination):not(.pager):not(.list-group):not(.list-unstyled):not(.list-inline) {
    list-style: none;

    li:before {
      .fa();
      font-size: 12px;

      content: @fa-var-play;
      position: absolute;
      margin-left: -18px;
      margin-top: 1px;

      .opacity(0.9);

      line-height: floor((ceil((@main-font-size * @font-scale-xs)) * @line-height-base));

      @media (min-width: @grid-float-breakpoint) {
        line-height: floor((@main-font-size * @line-height-base));
      }
    }

    ul li:before {
      .opacity(0.8);
    }

    ul ul li:before {
      font-size: (0.8 * 12px);
      .opacity(0.7);
    }

    ul ul ul li:before {
      .opacity(0.6);
    }
  }
}